form {
  background: antiquewhite;
  display: block;
  width: 70%;
  height: auto;
  margin: auto;
}

.form__div,
.form__leyend {
  display: flex;
  justify-content: center;
  margin: 1rem;
  font-size: 1.2rem;
}

.form__input__description {
  min-width: 25rem;
  max-width: 25rem;
  min-height: 8rem;
  max-height: 8rem;
  border: none;
  outline: none;
}
.form__danger {
  border: 1px solid lightcoral;
  box-shadow: 0 0 5px lightcoral;
}

.form__input,
.form__input__description {
  padding: 5px;
  border-radius: 0.5rem;
}

.form__input__URL {
  width: 25rem;
  padding: 0.5rem;
  border-radius: 0.5rem;
}

